
*{
box-sizing:  0;
}

body{
    margin:0;
    font-family:Inter, sans-seriff;
}


/* navbar */
.nav{
    background-color:#F55A5A;
    display: flex;
    align-items: center;
    justify-content:center;
    gap:0.438em;
    color:white;
}

.nav--title{

    font-size: 0.938em;
}


/* card */

.card--list{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}

.card{

    display:flex;
    align-items: center;

    flex-wrap: wrap;
    
    margin:auto;
    padding: 1.563em 0 1.125em 0;
    gap:1.25em;
    max-width: 52em;
}

.card--image{
    border-radius: 0.625em;
    height:15.625em;
    width:18.75em;
    flex-wrap: wrap;

}

.card--body{
    max-width: 4    1.875em;

}


.card--head{
    display:flex;
    align-items: center;
    gap:0.625em;
    flex-wrap: wrap;

}

.card--logo{
    height: 0.625em;
    background: #DADADA;
}

.card--location{
    font-style: normal;
font-weight: 400;
line-height: 0.75em;
letter-spacing: 0.17em;
height:0.625em;
color:#2B283A;


}

.card--google_url{
    text-decoration-line: underline;
    font-weight: 400;
    line-height: 0.75em;
    font-style: normal;
    color:#918E9B;
height: 0.813em;



}


.card--title{

    font-weight: 700;
font-size: 2.188em;
line-height: 1.875em;
color: #2B283A;
font-style: normal;
margin:0 0 0.05em 0;
}

.card--date{
    display:flex;
    font-style: normal;
font-weight: 700;
line-height: 0.75em;
color: #2B283A;
}

.card--description{
    margin:0;
    font-style: normal;
font-weight: 400;
line-height: 150%;
color: #2B283A;
max-width: 31.47em;
}
